

















































































































































































































import Vue from 'vue';
import FleetDetail from '@/components/fleet/FleetDetail.vue';
import Fleet from '@/classes/fleet/fleet';

export default Vue.extend({
  name: 'FleetInfoHeader',
  components: {
    FleetDetail,
  },
  props: {
    value: {
      type: Fleet,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    unionFleet: {
      type: Fleet,
    },
    isUnion: {
      type: Boolean,
      default: false,
    },
    admiralLv: {
      type: Number,
      default: 120,
    },
  },
  data: () => ({
    detailDialog: false,
    destroyDialog: false,
    lastTab: 'status',
    showTooltip: false,
    isMobile: true,
  }),
  mounted() {
    //
  },
  computed: {
    contactRate(): string {
      if (this.isUnion && this.index <= 1 && this.unionFleet) {
        return this.unionFleet.getContactRates(true)[0].sumRate.toFixed(1);
      }
      return this.value.getContactRates()[0].sumRate.toFixed(1);
    },
    nightContactRate(): string {
      if (this.isUnion && this.index <= 1 && this.unionFleet) {
        return (100 * this.unionFleet.nightContactRate).toFixed(1);
      }
      return (100 * this.value.nightContactRate).toFixed(1);
    },
    fleetScouts(): number[] {
      if (this.isUnion && this.index <= 1 && this.unionFleet) {
        return this.unionFleet.getUnionScoutScore(this.admiralLv).map((v) => Math.floor(100 * v) / 100);
      }
      return Fleet.getScoutScore(this.value.ships, this.admiralLv).map((v) => Math.floor(100 * v) / 100);
    },
    fleetSmoke(): number[] {
      if (this.isUnion && this.index <= 1 && this.unionFleet) {
        // return Fleet.getSmokeTriggerRate(this.value.ships);
        return Fleet.getSmokeTriggerRate2(this.unionFleet.ships);
      }
      // return Fleet.getSmokeTriggerRate(this.value.ships);
      return Fleet.getSmokeTriggerRate2(this.value.ships);
    },
    tpA(): number {
      if (this.isUnion && this.index <= 1 && this.unionFleet) {
        return Math.floor(this.unionFleet.tp * 0.7);
      }
      return Math.floor(this.value.tp * 0.7);
    },
    tp2A(): number {
      const showDebug = this.value.ships.every((v) => !v.isEscort);
      if (showDebug) {
        console.log('検証デバッグ用(そのうち消えます)');
      }

      let displayValue = 0;
      if (this.isUnion && this.index <= 1 && this.unionFleet) {
        if (showDebug) {
          console.log(`主力艦隊TP(戦車): INT(${Math.floor(1000 * this.unionFleet.mainTP2) / 1000}) = ${Math.floor(this.unionFleet.mainTP2)}`);
          console.log(`随伴艦隊TP(戦車): INT(${Math.floor(1000 * this.unionFleet.escortTP2) / 1000}) = ${Math.floor(this.unionFleet.escortTP2)}`);
          console.log(`連合艦隊TP(戦車): ${Math.floor(this.unionFleet.mainTP2)} + ${Math.floor(this.unionFleet.escortTP2)} = ${Math.floor(this.unionFleet.tp2)}`);
        }
        displayValue = Math.floor(this.unionFleet.tp2 * 0.7);
      } else {
        if (showDebug) {
          console.log(`艦隊TP(戦車): INT(${Math.floor(this.value.mainTP2)}) = ${Math.floor(this.value.tp2)}`);
        }
        displayValue = Math.floor(this.value.tp2 * 0.7);
      }

      if (showDebug) {
        console.log('');
      }
      return displayValue;
    },
    actualFleet(): Fleet {
      if (this.isUnion && this.index <= 1 && this.unionFleet) {
        return this.unionFleet;
      }
      return this.value;
    },
  },
  methods: {
    async clickedInfo() {
      this.destroyDialog = false;
      this.isMobile = window.innerWidth < 600;
      await (this.detailDialog = true);
      (this.$refs.fleetDetail as InstanceType<typeof FleetDetail>).tab = this.lastTab;
    },
    toggleDetailDialog() {
      if (!this.detailDialog) {
        this.lastTab = (this.$refs.fleetDetail as InstanceType<typeof FleetDetail>).tab;
        setTimeout(() => {
          this.destroyDialog = true;
        }, 100);
      } else {
        this.destroyDialog = false;
      }
    },
    closeDetail() {
      this.detailDialog = false;
      this.toggleDetailDialog();
    },
  },
});
